body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header{
text-align: center;
}
.header img{
    width: 10%;
    height: 80px;
}
.header nav{
    background-color: black;
    line-height: 40px;
    text-align: left;

}
nav a{
    text-decoration: none;
    color: white;
    margin: 20px;
    padding: 5px; 
}

nav a:hover{
    background-color: gray;
}
.Cart-Section{
    width: 100%;
    background-color: cornsilk;
    padding: 10px;
    position: relative;
}
.cart-top{
    text-align: center;
}
.Cart-Section .amount{
    float: right;
    
}
.Cart-Section h4{
    border-bottom: 1px dotted gray;
}
#special-offer{
    color: red;
}
#g-total{
    color: green;
}
#review-btn{
    width: 160px;
    height: 30px;
    background-color: goldenrod;
    border: 1px solid lightgray;
    position: relative;
    margin: 0;
    left: 25%;
}
.product{
    display: flex;
    margin-bottom: 20px;
    margin-left: 5px;
    margin-right: 5px;
    border-bottom: 1px solid lightgray;
    padding-bottom: 20px;
    
}
.product-details{
    background-color: lightgray;
    padding: 4px;
    width: 100%;
}
.product-img{
    padding-right: 20px;
}
.product-name{
    color: blue;
    font-weight: 400;
}
.add-to-cart-btn{
    background-color: goldenrod;
    width: 160px;
    height: 40px;
    border: 1px solid burlywood;
    border-radius: 5px;
}
.shop-container{
    display: flex;
    width: 100%;
}
.shop-container .product-container{
    width: 70%;
    margin-left: 20px;
    margin-right: 10px;
    border-right: 2px solid lightgray;
}
.shop-container .cart-container{
    width: 23%;
}

